<script setup>
import { ref, watch, computed, onMounted, onBeforeUnmount } from "vue";
import axios from "axios";
import { VueGoodTable } from "vue-good-table-next";
import "vue-good-table-next/dist/vue-good-table-next.css";
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.css";
import * as XLSX from "xlsx";
import VButton from '@/components/Button.vue'

import { useUserStore} from "@/stores/user.js";
import { log } from "@/utils/logger";
const userData = useUserStore();

const props = defineProps({
  settings: {
    type: Object,
    required: true,
  },
  urls: {
    type: Object,
    required: true,
  },
  fields: {
    type: Object,
    required: true,
  },
  canInsert: {
    type: Boolean,
    default: true
  },
  canUpdate: {
    type: Boolean,
    default: true
  }
});

const settings = ref();
const urls = ref();
const fields = ref();
const canInsert = ref();
const canUpdate = ref();

settings.value = props.settings;
urls.value = props.urls;
fields.value = props.fields;
canInsert.value = props.canInsert;
canUpdate.value = props.canUpdate;

const size = settings.value.size ?? [10,20,100];

if (Array.isArray(fields.value.toRemove)) {
  fields.value.toRemove.push("isEditing", "filterable", "vgt_id", "originalIndex");
} else {
  fields.value.toRemove = ["isEditing", "filterable", "vgt_id", "originalIndex"];
}

const datas = ref({data : [], dropdowns : []});

const columns = ref([]);
let insertColumns = ref([]);

const DataInsert = ref([]);
const filteredData = ref([]); // Store filtered data
const loading = ref(true);
const error = ref(null);
const firstColumnWidth = ref("150px");
const secondColumnWidth = ref("100px");
const changedRows = ref(new Map()); // Store changed rows
const changedRowsInsert = ref(new Map());

let fullUrl = urls.value.data;

if (urls.value.params) {
  const queryString = new URLSearchParams(urls.value.params).toString();
  fullUrl += `?${queryString}`;
}

const fetchAllDropdowns = async () => {
  const dropdownKeys = Object.keys(urls.value.dropdowns);

  for (const key of dropdownKeys) {
    const url = urls.value.dropdowns[key].url;
    const valueField = urls.value.dropdowns[key].valueField;
    const labelField = urls.value.dropdowns[key].labelField;
    
    try {
      const response = await axios.get(url);

    //   datas.value.dropdowns[labelField] = response.data.map(item => ({
    //     value: item[valueField] ?? "",
    //   }));
      datas.value.dropdowns[labelField] = response.data.map((item) => item[valueField] ?? "");
    } catch (error) {
      console.error(`Failed to fetch ${key} data from ${url}:`, error);
    }
  }
//   console.log("Fetched dropdown data:", Object.keys(datas.value.dropdowns));
};

const fetchData = async () => {
  try {
    loading.value = true;
    const response = await axios.get(fullUrl);
    if (response.data.length > 0) {
      const priorityList = fields.value.priority || [];
      const toRemoveList = fields.value.toRemove || [];
      const unpriorityList = fields.value.unpriority || [];

      const allKeys = Object.keys(response.data[0]);

      // 1. Prioritized keys
      const prioritizedKeys = priorityList.filter(
        (key) => allKeys.includes(key) &&
                key !== fields.value.firstCol.field &&
                !toRemoveList.includes(key) &&
                !unpriorityList.includes(key)
      );

      // 2. Unprioritized keys (explicitly listed in unpriority)
      const unprioritizedKeys = unpriorityList.filter(
        (key) => allKeys.includes(key) &&
                key !== fields.value.firstCol.field &&
                !toRemoveList.includes(key) &&
                !prioritizedKeys.includes(key)
      );

      // 3. Other keys (the rest)
      const otherKeys = allKeys.filter(
        (key) =>
          !prioritizedKeys.includes(key) &&
          !unprioritizedKeys.includes(key) &&
          key !== fields.value.firstCol.field &&
          !toRemoveList.includes(key) &&
          !fields.value.hidden.includes(key)
      );

      // Final order: priority → other → unpriority
      const orderedKeys = [...prioritizedKeys, ...otherKeys, ...unprioritizedKeys];
        columns.value = [
        {
            label: fields.value.firstCol.label,
            field: fields.value.firstCol.field,
            width: `${fields.value.firstCol.width ?? 80}px`,
            minWidth: "100px",
            sortable: true,
            filterable: false,
            editable: false,
            frozen: true,
            type: fields.value.firstCol.type,
            headerClass: "custom-header",
        },
        ...(fields.value.status
              ? [
                  {
                    label: fields.value.status.label ?? fields.value.status.field.toUpperCase(),
                    field: fields.value.status.field,
                    sortable: false,
                    filterable: true,
                    filterOptions: { enabled: true, placeholder: 'status' },
                    editable: false,
                    type: "text",
                    width: `${fields.value.status.width ?? 120}px`,
                    minWidth: "100px",
                    headerClass: "custom-header",
                  }
                ]
              : []
            ),
        ...orderedKeys.map((key) => {
            const isCheckbox = fields.value.checkbox.includes(key);

            const isNumeric =
                !isCheckbox &&
                response.data.every((row) => {
                const value = row[key];
                return !isNaN(Number(value));
                });
            const columnDef = fields.value?.columns?.find(col => col.column === key);

            return {
              label: key.replace(/_/g, " ").toUpperCase(),
              field: key,
              sortable: true,
              filterable: true,
              filterOptions: { enabled: true, placeholder: `${key}` },
              editable: canUpdate.value && !(fields.value?.uneditable?.includes?.(key)),
              type: isCheckbox ? "checkbox" : isNumeric ? "number" : "text",
              width: `${columnDef?.width ?? (
                Math.max(
                  ...response.data.map(item => String(item?.[key] ?? "").length),
                  key.length
                ) * size[0] + size[1]
              )}px`,
              minWidth: `${columnDef?.minWidth ?? size[2]}px`,
              headerClass: "custome-header",
            };
            }),
        ];
    }
    insertColumns = computed(() =>
        columns.value.map(col => ({
            ...col,
            filterOptions: { enabled: false },
            sortable: false,
        }))
    );
    datas.value.data = response.data.map((item) => ({
      ...item,
      filterable: true,
    }));
    datas.value.data = response.data.map((item) => {
        const formatted = {};

        for (const key in item) {
            const value = item[key];

            if (value === null || value === undefined) {
            formatted[key] = value;
            } else {
            const num = Number(value);
            formatted[key] = isNaN(num) ? String(value) : num;
            }
        }
        if (fields.value.status && fields.value.status.calculate) {
          formatted.status = fields.value.status.calculate(item);
        }

        // Add your extra field like this
        formatted.filterable = true;

        return formatted;
    });


    filteredData.value = [...datas.value.data]; // Initialize filtered data

    setTimeout(updateColumnWidths, 500);
  } catch (err) {
    error.value = "Failed to fetch data";
    console.error(err);
  } finally {
    loading.value = false;
    addNewRow();
  }
  // console.log(datas.value);
};

const addNewRow = () => {
  const newRow = {};
  columns.value.forEach((col) => {
    newRow[col.field] = fields.value.checkbox.includes(col.field) ? 0 : "";
  });
  newRow.id = DataInsert.value.length + 1;
  DataInsert.value.push(newRow);
};

// Track changes
const trackUpload = (row, field) => {
  if (!changedRowsInsert.value.has(row.id)) {
    changedRowsInsert.value.set(row.id, { ...row });
  } else {
    changedRowsInsert.value.get(row.id)[field] = row[field];
  }
};

const upload = async () => {
  try {
    const uploads = Array.from(changedRowsInsert.value.values());
    for (const item of uploads) {
      const uploadItem = { ...item };
      for (const prop of fields.value.toRemove) {
        delete uploadItem[prop];
      }
      delete uploadItem["id"];
      // console.log(uploadItem);
      fields.value.date.forEach((key) => {
        if (
          uploadItem[key] !== null &&
          uploadItem[key] !== undefined &&
          !isNaN(Date.parse(uploadItem[key]))
        ) {
          uploadItem[key] = new Date(uploadItem[key])
            .toISOString()
            .split("T")[0]; // Format: YYYY-MM-DD
        }
      });
      Object.keys(uploadItem).forEach((key) => {
        if (uploadItem[key] === "") {
          uploadItem[key] = null;
        }
      });
      await axios.post(urls.value.data, uploadItem);
      await fetchData();
    //   alert("Data uploaded Successfully");
      DataInsert.value = [];
      changedRowsInsert.value.clear();
      window.location.reload();
    }
  } catch (err) {
    console.error("Failed to upload: ", err);
    alert("Failed to upload");
  }
};

// For updates
const trackChanges = (row, field) => {
  if (!changedRows.value.has(row.id)) {
    changedRows.value.set(row.id, { ...row });
  } else {
    changedRows.value.get(row.id)[field] = row[field];
  }
};
// Save all changes (updates)
const saveChanges = async () => {
  const updates = Array.from(changedRows.value.values());
  const failedUpdates = [];

  for (const item of updates) {
    try {
      const updatedItem = Object.fromEntries(
        Object.entries(item).filter(
          ([key]) =>
            !fields.value.exclude.includes(key) &&
            !fields.value.toRemove.includes(key)
        )
      );

      fields.value.date.forEach((key) => {
        if (
          updatedItem[key] !== null &&
          updatedItem[key] !== undefined &&
          !isNaN(Date.parse(updatedItem[key]))
        ) {
          updatedItem[key] = new Date(updatedItem[key])
            .toISOString()
            .split("T")[0];
        }
      });

      await axios.put(urls.value.data, updatedItem);
    } catch (err) {
      console.error("Failed to update row:", item.id, err);
      failedUpdates.push(item.id);
    }
  }

  if (failedUpdates.length > 0) {
    alert(`Some rows failed to save: ${failedUpdates.join(", ")}`);
  } else {
    alert("Changes saved successfully!");
    window.location.reload();
    changedRows.value.clear();
  }
};



// Export to Excel (filtered data only)
const exportToExcel = () => {
  const worksheet = XLSX.utils.json_to_sheet(filteredData.value);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Marketing Data");
  XLSX.writeFile(workbook, "Filtered_Marketing_Data.xlsx");
};

// Update filtered data when table is filtered
const onTableFiltered = (filteredRows) => {
  filteredData.value = filteredRows;
  log("Length : " + filteredData.value.length);
  log("onTableFiltered : ", filteredData.value);
  // console.log("F Len : ", filteredData.value.length, " | M Len : ", datas.value.data.length);
};

// Dynamic Column Width Calculation
const updateColumnWidths = () => {
  const secondCol = document.querySelector(".vue-good-table th:nth-child(1)");
  if (secondCol) secondColumnWidth.value = secondCol.offsetWidth + "px";
  document.documentElement.style.setProperty(
    "--second-column-width",
    secondColumnWidth.value
  );
};

const getLoading = () => {
  return loading.value;
}

defineExpose({
  datas,
  loading,
  getLoading
});

onMounted(() => {
    fetchAllDropdowns();
    fetchData();
    // console.log(datas.value.dropdowns);

  window.addEventListener("resize", updateColumnWidths);
});

onBeforeUnmount(() => {
  window.removeEventListener("resize", updateColumnWidths);
});

const formatDate = (value) => {
  if (value) {
    const [date, time] = value.split(' ');  // Split the date and time
    const formattedDate = `${date}T${time.slice(0, 5)}`;  // Extract 'YYYY-MM-DDTHH:MM'
    return formattedDate; // For example: '2025-04-22T11:41'
  }
  return ''; // Return empty string if value is null or undefined
};
const unformatDate = (value) => {
    if (value) {
      const [date, time] = value.split('T');
      const formattedTime = `${time}:00`;  // Add seconds to the time
      return `${date} ${formattedTime}`; // For example: '2025-04-22 11:41:00'
    }
    return ''; // Return empty string if value is null or undefined
};
const onDateChanges = (event, row, field) => {
    row[field] = unformatDate(event.target.value); // Update value in the row
    trackChanges(row, field); // Track the changes
};
const onDateUpload = (event, row, field) => {
    row[field] = unformatDate(event.target.value); // Update value in the row
    trackUpload(row, field); // Track the changes
};
const showForm = ref(false);
</script>

<template>
  <div class="container-global mb-4">
    <h2>{{ settings.title }}</h2>
    <button v-if="canInsert" @click="upload">Upload</button>
    <button v-if="canInsert" @click="addNewRow">Add New Row</button>
    <p v-if="loading">Loading...</p>
    <p v-if="error" class="error">{{ error }}</p>
    <!-- INSERT TABLE -->
    <div class="table-container">
      <vue-good-table
        v-if="!loading && !error && canInsert"
        :columns="insertColumns"
        :rows="DataInsert"
        :search-options="{ enabled: false }"
        :sort-options="{ enabled: false }"
        :line-numbers="false"
        :select-options="{ enabled: false }"
        :pagination-options="{ enabled: false }"
        style="overflow-x: auto"
      >
        <template v-slot:table-row="props">
          <template v-if="fields.checkbox.includes(props.column.field)">
            <div class="checkbox-container">
              <input
                type="checkbox"
                name="custom-checkbox"
                class="custom-checkbox"
                :checked="props.row[props.column.field] == 1"
                @click="(event) => {
                    props.row[props.column.field] = event.target.checked ? 1 : 0;
                    trackUpload(props.row, props.column.field);
                  }"
                />
            </div>
          </template>
          <template v-else-if="Object.keys(datas.dropdowns).includes(props.column.field)">
            <Multiselect
                v-model="props.row[props.column.field]"
                :options="datas.dropdowns[props.column.field] || []"
                :searchable="true"
                :allow-empty="false"
                :placeholder="`Select ${props.column.field}`"
                @update:modelValue="trackUpload?.(props.row, props.column.field) || trackChanges?.(props.row, props.column.field)"
            />
            </template>
          <template v-else-if="fields.date.includes(props.column.field)">
            <input
              type="date"
              v-model="props.row[props.column.field]"
              @input="trackUpload(props.row, props.column.field)"
            />
          </template>
          <template v-else-if="fields.datetime.includes(props.column.field)">
            <input
              type="datetime-local"
              :value="formatDate(props.row[props.column.field])"
              @input="onDateUpload($event, props.row, props.column.field)"
            />
          </template>
          <template v-else-if="props.column.editable !== false">
            <input
              v-model="props.row[props.column.field]"
              @input="trackUpload(props.row, props.column.field)"
            />
          </template>
          <template v-else>
            <span>{{ props.row[props.column.field] }}</span>
          </template>
        </template>
      </vue-good-table>
    </div>
  <div class="mb-2">
    <VButton @click="exportToExcel"type="submit" color="danger" size="md" :block="false" class="me-2">Export to Excel</VButton>
   <!-- <VButton @click="showForm = true" color="danger" class="me-2">Add New</VButton> -->
    <VButton v-if="canUpdate" @click="saveChanges" type="submit" color="danger" size="md" :block="false">Save Updates</VButton>
  </div> 
 <!-- <div v-if="showForm" class="modal-form">
  <button class="close-btn" @click="showForm = false">✖</button>
  <MiniForm
    v-if="settings.canInsert"
      v-model="formData"
      :fields="fields"
      :urls="urls"
    />
</div>-->

    <!-- UPDATE TABLE -->
    <div class="table-container">
      <vue-good-table
        v-if="!loading && !error"
        :columns="columns"
        :rows="datas.data"
        :pagination-options="{ enabled: true, mode: 'pages', perPage: 10 }"
        :search-options="{ enabled: true }"
        :sort-options="{ enabled: true }"
        style="overflow-x: auto"
        @on-filtered="onTableFiltered"
        styleClass="vgt-table striped"
      >
        <template v-slot:table-row="props">
          <template v-if="fields.checkbox.includes(props.column.field)">
            <div class="checkbox-container">
              <input
                type="checkbox"
                class="custom-checkbox"
                :checked="props.row[props.column.field] == 1"
                @click="(event) => {
                    props.row[props.column.field] = event.target.checked ? 1 : 0;
                    trackChanges(props.row, props.column.field);
                  }"
                />
            </div>
          </template>
          <template v-else-if="Object.keys(datas.dropdowns).includes(props.column.field) && canUpdate">
            <Multiselect
                v-model="props.row[props.column.field]"
                :options="datas.dropdowns[props.column.field] || []"
                :searchable="true"
                :allow-empty="false"
                :placeholder="`Select ${props.column.field}`"

                noResultsText="No matching results"
                @update:modelValue="trackUpload?.(props.row, props.column.field) || trackChanges?.(props.row, props.column.field)"
            />
            </template>
          <!-- <template v-else-if="props.column.field === 'name_customer'">
            <Multiselect
              v-model="props.row[props.column.field]"
              :options="datas.dropdowns.name_customer || []"
              :searchable="true"
              :allow-empty="false"
              placeholder="Select customer..."
              noResultsText="No matching customers found"
              @update:modelValue="trackChanges(props.row, props.column.field)"
            />
          </template>
          <template v-else-if="props.column.field === 'location'">
            <Multiselect
              v-model="props.row[props.column.field]"
              :options="datas.dropdowns.location || []"
              :searchable="true"
              :allow-empty="true"
              placeholder="Select location..."
              noResultsText="No matching customers found"
              @update:modelValue="trackChanges(props.row, props.column.field)"
            />
          </template> -->
          <template v-else-if="fields.date.includes(props.column.field)">
            <input
              type="date"
              v-model="props.row[props.column.field]"
              @input="trackChanges(props.row, props.column.field)"
            />
          </template>
          <template v-else-if="fields.datetime.includes(props.column.field)">
            <input
              type="datetime-local"
              :value="formatDate(props.row[props.column.field])"
              @input="onDateChanges($event, props.row, props.column.field)"
            />
          </template>

          <template v-else-if="props.column.editable !== false">
            <input
              v-model="props.row[props.column.field]"
              @input="trackChanges(props.row, props.column.field)"
            />
          </template>
          <template v-else>
            <span>{{ props.row[props.column.field] }}</span>
          </template>
        </template>
      </vue-good-table>
    </div>
  </div>
</template>

<style>
.card{
  border: #ffffff;
}
/* .table-container {
  width: 100%;
  overflow-x: auto;
  max-width: 100%;
  position: relative;
  margin-top: "200px";
  height: 100%;
  padding: 0px 0px;
} */

.vgt-table th { /* table header */
  padding: 0.5px 5px;
  font-size: 13px;
  font-weight: bold;
  text-align: center;
}

table.vgt-table td {/* table row / cell */
  padding: 5px 5px;
}

.checkbox-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75%;
  padding: 1px;
}
.checkbox{
  color:#000000
}

.error {
  color: red;
}
input {
  padding: 0px 0px;
  font-size: 12px; /* font size */
  width: 100%;
  margin: 0px 0px;
  border: none !important;
  outline: none !important;
  height: 100%;
}

.vgt-wrap__footer .footer__row-count__label,
.vgt-wrap__footer .footer__row-count__select,
.vgt-wrap__footer .footer__navigation__page-btn span,
.vgt-wrap__footer .footer__navigation__page-info span {
    font-size: 12px;
}

.multiselect__content-wrapper {
  position:absolute !important;
  z-index: 9999 !important;
  min-height: 100px !important;
  max-height: 100px !important;
  overflow-y: auto !important;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}
.custom-checkbox {
  appearance: none;
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid #db0000;
  border-radius: 3px;
  cursor: pointer;
  position: relative;
  background-color: #ebe1e1;
}

.custom-checkbox:checked {
  background-color: #dc3545;
  border-color: #ffffff;
}

.custom-checkbox:checked::after {
  content: '✔';
  color: rgb(255, 255, 255);
  font-size: 12px;
  position: center;
  left: 2px;
  top: -1px;
}
.vgt-left-align {
  font-size: 12px;
  color: #1a1a1a !important;
}
.vgt-right-align {
  font-size: 12px;
  color: #1a1a1a !important;
}

.modal-form {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4); /* backdrop */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-form > * {
  background: white;
  padding: 20px;
  border-radius: 8px;
  max-height: 90vh;
  overflow-y: auto;
  width: 90%;
  max-width: 800px;
}

.close-btn {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 22px;
  color: white;
  cursor: pointer;
}

/* Light Mode (default) */
.table-container {
  background-color: #fff;
  color: #000;
}

.vgt-table {
  background-color: #fff;
  color: #000;
}

.vgt-table th,
.vgt-table td {
  background-color: #fff;
  color: #000;
}

/* Dark Mode */
.dark .table-container {
  background-color: #fff;
  color: #000;
}

.dark .vgt-table {
  background-color: #fff;
  color: #000;
}

.dark .vgt-table th,
.dark .vgt-table td {
  background-color: #fff;
  color: #000;
}

.dark .vgt-left-align,
.dark .vgt-right-align {
  background-color: #fff;
  color: #000;
}

/* Footer */
.dark .vgt-wrap__footer {
  background-color: #fff;
  color: #000;
}

.dark .vgt-wrap__footer select {
  background-color: #fff;
  color: #000;
}

</style>
